@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;400&display=swap');

$primary-color: #22254b;
$secondary-color: #373b69;

.body {
    overflow-y: auto;
    height: 100vh;
    background-color: $primary-color;
    font-family: 'Poppins', sans-serif;
    margin: 0;

    header {
        padding: 1rem;
        display: flex;
        justify-content: flex-end;
        background-color: $secondary-color;
    }

    main {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        overflow: auto;

    }

}




.search {
    background-color: transparent;
    border: 2px solid $primary-color;
    border-radius: 50px;
    font-family: inherit;
    font-size: 1rem;
    padding: 0.5rem 1rem;
    color: #fff;
}

.search::placeholder {
    color: #7378c5;
}

.search:focus {
    outline: none;
    background-color: $primary-color;
}

.movie {
    width: 300px;
    margin: 1rem;
    background-color: $secondary-color;
    box-shadow: 0 4px 5px rgba(0, 0, 0, 0.2);
    position: relative;
    overflow: hidden;
    border-radius: 3px;

    img {
        width: 100%;
    }
}

.movie:hover {
    .overview {
        transform: translateY(0);
    }
}

.movieInfo {
    color: #eee;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 0.2rem;
    padding: 0.5rem 1rem 1rem;
    letter-spacing: 0.5px;

    h3 {
        margin-top: 0;
        margin-bottom: 1em;
    }

    span {
        background-color: $primary-color;
        padding: 0.25rem 0.5rem;
        border-radius: 3px;
        font-weight: bold;
    }

    span.green {
        color: lightgreen;
    }

    span.orange {
        color: orange;
    }

    span.red {
        color: red;
    }

}

.overview {
    background-color: #fff;
    padding: 2rem;
    position: absolute;
    left: 0;
    bottom: 0;
    right: 0;
    max-height: 100%;
    transform: translateY(101%);
    overflow-y: auto;
    transition: transform 0.3s ease-in;
}